* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: 'Oswald', sans-serif;
}
.header {
    background-color: #673AB7;
    color: white;
    text-align: center;
    padding: 30px;
    font-size: 20px;
    letter-spacing: 2px;
}
.clicker {
    border-radius: 5px;
    box-shadow: 0 0 10px black;
    width: 80%;
    max-width: 300px;
    margin: 60px auto;
    text-align: center;
}
.result {
    padding: 20px;
    font-size: 60px;
    background-color: deepskyblue;
    color: white;
}
.buttons {
    display: flex;
    flex-shrink: 1;
}
button {
    flex-basis: 50%;
    color: white;
    padding: 20px 0;
    font-size: 26px;
    border: none;
    cursor: pointer;
    transition: .5s;
}

.btn__add {
    background-color: green;
}

.btn__clear {
    background-color: #ffa812;
    color: black;
}

.btn__subtract {
    background-color: #d81305;
}

@media (min-width: 1024px) {
    .btn__add:hover {
        background-color: rgb(20, 190, 20);
    }

    .btn__clear:hover {
        background-color: #fbff00;
        color: black;
    }

    .btn__subtract:hover {
        background-color: #f87249;
    }
}
